body {
  background-color: rgb(219, 215, 195);
  height: 100vh;
}

.title-nav-container {
  display: flex;
}

#website-title {
  font-family: 'Merriweather', serif;
  text-align: left;
  border: solid 1px black;
  padding: 20px;
  cursor: pointer;
}

.navbar {
   background-color: rgb(219, 215, 195);
   display: flex;
   justify-content: center;
   align-items: center;
   position: relative;
   left: 5%;
   width: 70%;
}

.navbar button {
  height: 40%;
  background-color: rgb(219, 215, 195);
  color: #6e6e6e;
  padding: 12px 20px;
  font-size: 20px;
  border: 1px;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-family: 'Merriweather', serif;
  
}

.navbar button:hover {
  background-color: rgb(212, 208, 186);
}

#loading-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(212, 208, 186);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-top: 4px solid #e5f4ff;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}


.title h2 {
  font-family: 'Merriweather', serif;
  font-size: 24px;
  margin-bottom: 5px;
  border-bottom: 1px solid black;
  padding-bottom: 5px;
}

.title p {
  font-family: 'Merriweather', serif;
  font-style: italic;
  margin-top: 5px;
}

.github-link-container {
  position: fixed;
  bottom: 5px;
}

.github-link {
  color: #333;
  font-size: 24px;
}

// cents per kwh chart
#cents-per-kwh-chart-container {
  display: none;
  align-items: center;
  justify-content: center;
}

#cents-per-kwh-chart-title {
  position: relative;
  left: 20%;
  top: 25%;
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 20%;
}

#cents-per-kwh-chart {
  width: 90vw;
  height: 75vh;
  margin-top: -9%;
}

.tooltip {
    position: absolute;
    padding: 10px;
    background-color: steelblue;
    color: white;
    border: 1px solid white;
    border-radius: 10px;
    display: none;
    opacity: .75;
}

.listening-rect {
      pointer-events: all;
      fill-opacity: 0;
      stroke-opacity: 0;
      z-index: 1;
}

.tooltip-line {
  stroke-opacity: 0.5;
} 


// total consumption
#total-consumption-chart-container {
  display: none;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  max-height: 80vh;
}

#total-consumption-title {
   width: 50%;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}

#total-consumption-chart {
  width: 50%;
  margin-top: -2%;
  margin-right: 2%;
}

#pie-chart-slider {
  width: 20%;
  margin: 20px;
}

#selected-year {
  font-family: 'Merriweather', serif;
  font-size: 24px;
}


// net energy
#net-energy-chart-container {
  display: none;
  align-items: center;
}

#net-energy-chart-title {
  position: relative;
  bottom: 100px;
  left: 5%;
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 20%;
  min-width: 20%;
  margin-right: -17%;
}

#energy-chart-form {
  font-family: 'Merriweather', serif;
}

#net-energy-chart {
  width: 95vw;
  margin-top: 5%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80%;
}

